@import "mixins";

body,
html {
    background: #fff;
    color: #000000;

    font-family: 'Gloria Hallelujah', cursive;
}


.hero {
    background: url(../img/bg-big.jpg) no-repeat scroll 50% 50% #ccc;
    background-size: cover;

    color: #fff;

    min-height: 400px;
    text-align: center;

    @include tabletUp() {
        height: 600px;
        text-align: left;
    }

    &__title {
        color: currentColor;
        margin-top: 50%;
        transform: translateY(-50%);
        text-shadow: 2px 2px 2px #000;

        @include tabletUp() {
            margin-top: 40%;
        }
    }

    &__text {
        color: currentColor;
        font-size: 24px;
        text-shadow: 2px 2px 2px #000;
    }
}

.text-block {
    margin: 40px 0;

    &__title {
        line-height: 3;
    }

    &__text {
        line-height: 2.5;
    }
}